<template>
    <div class="examples">
        <div class="imglogo">
            <img class="banner" src="../../style/images/mine.jpg"/>
            <img class="portrait" :src="person[0].img" alt="">
            <div class="name">
                <div class="chinesename">{{person[0].chinesename}}</div>
                <div class="englishname">{{person[0].englishname}}</div>
            </div>
        </div>
        <div class="history">
            <van-cell title-style="margin-left:10px">
            <van-icon class-prefix="icon" class="iconfont icon" name="jilu" color="#ffa500"/>
            <div class="title">发&nbsp;送&nbsp;记&nbsp;录</div>
            </van-cell>
        </div>
        <div class="programme">
            <van-collapse v-model="activeNames" accordion>
                <van-collapse-item v-for="(item,index) in history" :name=index :key="index">
                    <div slot="title">
                        <span class="index">{{ index < 9 ? ('0' + (index + 1)) : (index + 1) }}</span>
                        <span class="title">{{ item.sendName }}</span>
                        <span class="data">{{ item.sendTime }}</span>
                    </div>
                    <div>
                        已发送地址：
                        <div v-for="(address, index1) in item.address" :key="index1" class="address">{{address}}</div>
                    </div>
                </van-collapse-item>
            </van-collapse>
        </div>
        <van-goods-action>
            <van-goods-action-icon>
                <van-icon slot="icon" class="iconfont" class-prefix='icon' name="shouye" @click="toHome"/>
                首页
            </van-goods-action-icon>
            <van-goods-action-icon url="https://www.continental-industry.com/zh-CN/TopNavi/Company/ContiTech-Division/Location-Profiles/China">
                <van-icon slot="icon" class="iconfont" class-prefix='icon' name="lianxiwomen" />
                联系我们
            </van-goods-action-icon>
            <van-goods-action-icon url="https://www.continental-industry.com">
                <van-icon slot="icon" class="iconfont" class-prefix='icon' name="guanwang" />
                官网
            </van-goods-action-icon>
            <van-goods-action-icon>
                <van-icon slot="icon" class="iconfont" class-prefix='icon' name="wode" @click="toMine"/>
                我的
            </van-goods-action-icon>
        </van-goods-action>
    </div>
</template>
<script>
    import {Cell, CellGroup,Tabbar, TabbarItem,Icon,GoodsAction, GoodsActionIcon, GoodsActionButton,Collapse, CollapseItem} from 'vant';
    import { getSendRecords } from '@/api/records';
    import { getUserInfo } from '@/api/user'
    export default {
        components: {
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [Tabbar.name]: Tabbar,
            [TabbarItem.name]: TabbarItem,
            [Icon.name]: Icon,
            [GoodsAction.name]: GoodsAction,
            [GoodsActionIcon.name]: GoodsActionIcon,
            [GoodsActionButton.name]: GoodsActionButton,
            [Collapse.name]: Collapse,
            [CollapseItem.name]: CollapseItem,
        },
        data() {
            return{
                active: 0,
                person:[{img:'',chinesename:"大陆集团康迪泰克",englishname:"Continental Industry"}],
                activeNames: ['1'],
                history: []
            }
        },

        mounted() {
            getUserInfo().then(res=>{
                if (res.status === 200) {
                    this.person[0].img = res.data.headimgurl
                    this.person[0].chinesename = res.data.nickname
                }
            })
            getSendRecords({}).then( res => {
                if (res.status === 200) {
                    this.history = res.data;
                    this.history.forEach(item => {
                        item.sendTime = item.sendTime.split('T')[0];
                        item.address = item.sendEmail.split(',');
                    })
                }
            })
        },

        methods:{
            toHome() {
                this.$router.push({path: '/home'})
            },
            toMine() {
                this.$router.push({path: '/mine'})
            }
        }
    }
</script>
<style lang="less" scoped>
    .examples {
        margin-bottom: 16.33333vw;
        .imglogo {
            box-shadow: 1px 8px 1px rgba(128, 128, 128, 0.15);
            position: relative;
            .name {
                width: 50%;
                height: auto;
                position: absolute;
                left: 90px;
                top: 160px;
                .chinesename {
                    font-weight: 600;
                }
                .englishname {
                    color: #888888;
                    font-size: 8px;
                }
            }
            img {
                width: 100%;
            }
            .portrait {
                width: 60px;
                height: 60px;
                position: relative;
                top: -25px;
                left: 20px;
            }
        }
        .history {
            box-shadow: 1px 8px 1px rgba(128, 128, 128, 0.15);
            padding-top: 8px;
            .van-cell {
                background-color: transparent;
                /*.icon {*/
                    /*position: absolute;*/
                    /*left: 10px;*/
                /*}*/
                .title {
                    position: absolute;
                    left: 40px;
                    bottom: 0px;
                }
            }
        }
        .programme {
            margin-top: 8px;
            .title {
                font-size: 16px;
                letter-spacing:2px;
                font-weight: 600;
            }
            .index {
                color: rgb(255, 165, 0);
                margin-right: 10px;
                font-size: 16px;
            }
            .data {
                margin-left: 5px;
                color: #888888;
            }
            .address {
                position: relative;
                left: 80px;
                bottom: 20px;
            }
        }
        /deep/.van-goods-action {
            box-shadow: 0px -10px 54px #9697999c;
            border-top-left-radius: 17px;
            border-top-right-radius: 17px;
            height: 16.33333vw;
            .van-goods-action-icon {
                border-top-left-radius: 17px;
                border-top-right-radius: 17px;
                width: 25%;
                color: #ffa500;
                .iconfont {
                    color: #ffa500;
                    font-size: 7.26667vw;
                }
                .van-goods-action-icon__icon {
                    width: 2em;
                }
            }
        }

        /deep/.van-collapse-item__content {
            padding-left: 12.26667vw;
            padding-bottom: 0;
            padding-top: 10px;
        }
    }
</style>
